
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


import dateFormat from 'vux/src/tools/date/format.js'
import querystring from 'vux/src/tools/querystring/index.js'
import numberComma from 'vux/src/tools/number/comma.js'


import {
  mapState
} from 'vuex'

import FullImage from '@/components/IFullImage/index'
import '../assets/css/wechat.css';

export default {
  components: {
    Swiper,
    SwiperItem,
    XInput,
    XButton,
    FullImage
  },
  data() {
    return {
      now: '00:00',
      luckyList: []
    }
  },
  computed: {

  },
  methods: {
    init() {
      for (var i = 0; i < 30; i++) {
        this.luckyList.push({
          floor: 16 + i * 200,
          nickname: '',
          prov: '',
          city: '',
          header: 'data:image/jpeg;base64,',
          time: '2017-06-07 12:23'
        })
      }
      document.title = '';
    }
  },
  mounted() {
    this.init();
  }
}




