import {
  mapState
} from 'vuex'
export default {
  data() {
    return {
      mute: false,
    }
  },
  computed: {
    ...mapState({
      showVolume: state => state.showArrow
    }),
    player() {
      return this.$refs.audio;
    }
  },
  methods: {
    audioPlayer() {
      if (this.player.paused) {
        this.player.play();
      } else {
        this.player.pause();
      }
      this.mute = !this.mute;
    }
  },
  mounted() {
    this.player.volume = 0.4;
  },
  activated(){
    this.player.play();
  }
}




